import {request} from '@/common/js/request'

export default{
	data(){
		return {
			page: 0, //
			pageNum: 6, //
			loadingType: 1, //0 1 2
			isLoading: false, //
			loaded: false, //
		}
	},
	methods: {
		
		/**
		 * navigatorTo
		 * @param {String} url
		 * @param {Object} options
		 * @param {Boolean} options.login   
		 */
		navTo(url,type, query,checkLogin=false){
			this.$util.throttle(()=>{
				if(!url){
					return;
				}
				if(url=='back'){
					uni.navigateBack()
					return
				}
				// 
				if(checkLogin){
					uni.showToast({
						title:'',
						icon:'none'
					})
					return
				}
				if(type=='switch'){
					uni.switchTab({
						url:url
					})
				}else{
					let link=query? url+'?'+query:url
					uni.navigateTo({
						url:link
					})
				}
				
			}, 300)
		},
		/**
		 * $request
		 * @param {String} module
		 * @param {String} operation
		 * @param {Boolean} data 
		 * @param {Boolean} ext 
		 * @param {Boolean} ext.showLoading loading
		 * @param {Boolean} ext.hideLoading loading
		 * @param {Boolean} ext.login 
		 * @param {Boolean} ext.setLoaded 
		 */
		$request(module, operation, data={}, ext={}){
			if(ext.login && !this.$util.isLogin()){
				return;
			}
			if(ext.showLoading){
				this.isLoading = true;
			}
			return new Promise((resolve, reject)=> {
				request(module, operation, data, ext).then(result => {
					if(ext.hideLoading !== false){
						this.isLoading = false;
					}
					setTimeout(()=>{
						if(this.setLoaded !== false){
							this.loaded = true;
						}
					}, 100)
					this.$refs.confirmBtn && this.$refs.confirmBtn.stop();
					resolve(result);
				}).catch((err) => {
					reject(err);
				})
			})
		},
		imageOnLoad(data, key){
			setTimeout(()=>{
				this.$set(data, 'loaded', true);
			}, 100)
		},
		showPopup(key){
			this.$util.throttle(()=>{
				this.$refs[key].open();
			}, 200)
		},
		hidePopup(key){
			this.$refs[key].close();
		},
		stopPrevent(){},
	},
}